import React, { Component, Fragment } from 'react'
import { Link, Route } from 'react-router-dom'

import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import {AppBar, Typography, Toolbar} from '@material-ui/core'

import UserRegistro from './UserRegistro'
import UserEdit from './UserEdit'
import ListaUser from './ListaUser'


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
  }),
);

const ContentAdmin = (props: IProps) => {
    const classes = useStyles();
    

    return(
        <Fragment>
            <main className={classes.content}>
                <div className={classes.toolbar} />
                {/* <Link to="/1">Dash 1</Link>
                <Link to="/2">Dash 2</Link> */}
                
                <Route exact path='/app/registers' > 
                     <UserRegistro/> 
                </Route>
                <Route exact path='/app/edit' > 
                     <UserEdit /> 
                </Route>
                {/* <Route exact path='/app/users' > 
                    <ListaUser/> 
                </Route> */}

                <Route exact path='/app' > 
                    <ListaUser/> 
                    
                </Route>
            </main>
        </Fragment>
    )
}

interface IProps{

}

export default ContentAdmin