import React, { Component, Fragment } from 'react'
import { Link, Route } from 'react-router-dom'

import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import { Typography, Drawer, List, ListItem, ListItemIcon, ListItemText, Divider } from '@material-ui/core'

import UserIcon from '@material-ui/icons/People'


const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
    },
    appBar: {
      zIndex: theme.zIndex.drawer + 1,
    },
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
    },
    drawerPaper: {
      width: drawerWidth,
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
  }),
);


const MenuDrawerAdmin = (props: IProps) => {
    const classes = useStyles();

    return(
        <Fragment>
            <Drawer
            className={classes.drawer}
            variant="permanent"
            classes={{
                paper: classes.drawerPaper,
            }}>
                <div className={classes.toolbar} />
                <List >
                    <ListItem button key="1" component={Link} to="/app" >
                        <ListItemIcon>
                            <UserIcon />
                        </ListItemIcon>
                        <ListItemText primary="Lista de Usuarios"  />
                    </ListItem>
                    <ListItem button key="2" component={Link} to="/app/registers" >
                        <ListItemIcon>
                            <UserIcon />
                        </ListItemIcon>
                        <ListItemText primary="Registrar Usuarios"  />
                    </ListItem>
                
                </List>
                <Divider /> 
                <List>
                    <ListItem button key="100">
                        <ListItemIcon>
                            <UserIcon />
                        </ListItemIcon>
                        <ListItemText primary="Profile" />
                    </ListItem>
                </List>
            </Drawer>
        </Fragment>
    )
}

interface IProps{

}

export default MenuDrawerAdmin
