import React, { Component, Fragment } from 'react'
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import {AppBar, Typography, Toolbar, Button} from '@material-ui/core'

import {IUsuario} from '../../../interfaces/IUsuario'

const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
    },
    appBar: {
      zIndex: theme.zIndex.drawer + 1,
    },
    link: {
      margin: theme.spacing(1, 1.5),
    },
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
    },
    drawerPaper: {
      width: drawerWidth,
    },
    toolbar: {
      flexWrap: 'wrap',
    },
    toolbarTitle: {
      flexGrow: 1,
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    // necessary for content to be below app bar
    // toolbar: theme.mixins.toolbar,
  }),
);


const ToolBarMain = (props: IProps) => {
    const classes = useStyles();

    const clickSalir = () => {
      console.log('salir')
      props.updateLogueado(false)
     
    }
    return(
        <Fragment>
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar className={classes.toolbar}>
                {/* <Typography variant="h6" noWrap>
                    User: {props.usuario.nombre}
                </Typography>
                <Button color="primary" variant="outlined" className={classes.link} onClick={()=>clickSalir()}>
                    Salir de la Sesión
                  </Button> */}
                  <Typography variant="h6" color="inherit" noWrap className={classes.toolbarTitle}>
                    User: {props.usuario.nombre}
                  </Typography>
                  <nav>
                    
                  </nav>
                  <Button color="secondary" variant="contained" className={classes.link} onClick={()=>clickSalir()}>
                    Salir de la Sesión
                  </Button>
                </Toolbar>
            </AppBar>
        </Fragment>
    )
}

interface IProps{
  usuario: IUsuario
  updateLogueado: (logueado: boolean) => void
}

export default ToolBarMain
