import React, { Component, Fragment } from 'react'
import { Link, Route } from 'react-router-dom'
import { withStyles, Theme } from '@material-ui/core'

import ToolBarAdmin from '../components/ToolBarAdmin'
import MenuDrawerAdmin from '../components/MenuDrawerAdmin'
import ContentAdmin from '../components/ContentAdmin'

import {IUsuario} from '../../../interfaces/IUsuario'
import {IFacturacion} from '../../../interfaces/IFacturacion'

import {listar} from '../../../axios/UsuarioService'

const styles = (theme: Theme) => ({
    root: {
        display: 'flex',
    }
});

class Admin extends Component<IProps, IState> {
    constructor(props: IProps){
        super(props)
        this.state = {
            users: []
        }
    }

    async componentDidMount(){
        let result = await listar()
        console.log('result', result)
    }
    render(){
        return(
            <Fragment>
                <div style={{ display: 'flex'}}>
                    <ToolBarAdmin updateLogueado={this.props.updateLogueado} usuario={this.props.usuario}/>
                    <MenuDrawerAdmin/>
                    <ContentAdmin />
                </div>
            </Fragment>
        )
    }
}

interface IProps{
    usuario: IUsuario
    updateLogueado: (logueado: boolean) => void
}

interface IState{
    users: IUsuario[]
}

export default withStyles(styles)(Admin); 