import axiosInstance from './IAxios'
import {IUsuario} from '../interfaces/IUsuario'
import {ILogin} from '../interfaces/ILogin'

const login = async (login: ILogin): Promise<IResultadoLogin>  => {
    var resultado: IResultadoLogin = {
        
    }
    
    try{
        let query = await axiosInstance.post("usuario/login", login)
        console.log('axios result =>', query.data)
        resultado = query.data
    }catch(e){
        console.log('axios error =>', e)
        return resultado;
    }
    

    return resultado;
}

const registrar = async (user: IUsuario): Promise<IResultadoRegistrar> => {
    let resultado: IResultadoRegistrar = await axiosInstance.post("usuario/registrate", user)
    console.log('data a registrar', user)
    console.log('axios =>', resultado)
    return resultado;
}

const listar = async (): Promise<Array<IUsuario>> => {
    let resultado: Array<IUsuario> = await axiosInstance.get("usuario/")

    console.log('axios =>', resultado)
    return resultado;
}

const update = async (user: IUsuario): Promise<boolean> => {
    let result: any = await axiosInstance.put("usuario/", user);
    console.log('axios update =>', user)
    return result
}

const eliminarUser = async (id: number): Promise<boolean> => {
    let result: any = await axiosInstance.delete("usuario/"+id);
    console.log('axios delete =>', id)
    return result
}

interface IResultadoLogin{
    
        auth?: boolean
        token?: string
        usuario?: IUsuario
        message?: string
    
}
interface IResultadoRegistrar{
    data:{
        status: number
        operation: boolean
        token: string
    }
    
}


export {
    login,
    registrar,
    listar,
    update,
    eliminarUser
}