import React, { Component, Fragment } from 'react'
import { Switch, Route, BrowserRouter as Router, Link } from 'react-router-dom'

import CssBaseline from '@material-ui/core/CssBaseline'
import Button from '@material-ui/core/Button'

import LoginView from '../login/components/login'
import MainView from '../main/containers/index'
import HomeView from '../home/containers/index'
import AdminView from '../admin/containers/index'

import {IUsuario} from '../../interfaces/IUsuario'

class App extends Component<IProps, IState> {
    constructor(props: IProps){
        super(props)
        this.state = {
            logueado: false,
            usuario: {
                id: 0,

                username:"", 
                nombre: "",
                apellido: "",
                role:""
            }
        }
    }
    setUsuario = (usuario: IUsuario) => {this.setState({usuario})}
    setLogueado = (logueado: boolean) => {this.setState({logueado})}

    render() {
        
        return (
            <Fragment>
                <Router>
                    <Switch>
                        <Route  path="/">
                            {
                                (this.state.logueado) ?
                                    (this.state.usuario.role == "admin") ?
                                        <AdminView usuario={this.state.usuario} updateLogueado={this.setLogueado}/>
                                        :
                                        <MainView usuario={this.state.usuario} updateLogueado={this.setLogueado}/>
                                    :
                                    <LoginView 
                                        updateUsuario={this.setUsuario}
                                        updateLogueado={this.setLogueado} />
                            }
                            {/* <MainView/> */}
                        </Route>
                        {/* <Route exact path="/login2">
                            <LoginView/>
                        </Route> */}
                        {/* <Route path="/">
                            <HomeView/>
                        </Route> */}

                    </Switch>

                </Router>
                
            </Fragment>

        )
    }
}

interface IProps{
    
}

interface IState{
    logueado: boolean
    usuario: IUsuario
}

export default App