import React, { Fragment, useState } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { Link as LINK, Route, Redirect   } from 'react-router-dom'
import Draggable from 'react-draggable';

import { useHistory  } from 'react-router'; 
import { CssBaseline, Grid,  Link, Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions } from '@material-ui/core'
import { Avatar, Button, Typography, TextField, Box } from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Paper, { PaperProps } from '@material-ui/core/Paper';

import {IUsuario} from '../../../interfaces/IUsuario'
import {ILogin} from '../../../interfaces/ILogin'
import {login} from '../../../axios/UsuarioService'

function PaperComponent(props: PaperProps) {
  return (
    <Draggable handle="#draggable-dialog-title" cancel={'[class*="MuiDialogContent-root"]'}>
      <Paper {...props} />
    </Draggable>
  );
}


const useStyles = makeStyles((theme) => ({
    root: {
      height: '100vh',
    },
    image: {
      backgroundImage: 'url(https://source.unsplash.com/random)',
      backgroundRepeat: 'no-repeat',
      backgroundColor:
        theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
      backgroundSize: 'cover',
      backgroundPosition: 'center',
    },
    paper: {
      margin: theme.spacing(8, 4),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
  }));

const LoginView = (props: IProps) => {
    const classes = useStyles();
    const [username, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [open, setOpen] = React.useState(false);
    const [mensaje, setMensaje] = React.useState("");
    const validateForm = () => (username.length > 0 && password.length > 0);
    const handleSubmit = async(event:any) => {
      event.preventDefault()

      var usuarioNew :ILogin = {
        username: username,
        password: password
      }
      
      let result = await login(usuarioNew);
      console.log("result", result);
      if(result.auth == true ){
        console.log("if", result);
        var usuarioAux: IUsuario = {
          id: result.usuario?.id!,
          username: result.usuario?.username!,
          role: result.usuario?.role!,
          nombre: result.usuario?.nombre!,
          apellido: result.usuario?.apellido!
        }
        console.log("m ID", ""+result.usuario?.id!);
        localStorage.setItem('id', ""+result.usuario?.id!)
        console.log("m TK", result.token!);
        localStorage.setItem('token', result.token!)
        props.updateUsuario(result.usuario!)
        props.updateLogueado(true)
      }else{
        console.log("error", result.message);
        setMensaje(""+result.message!)
        setOpen(true)
      }

      // console.log("data", result);
      //props.updateLogueado(true)
      // return <Redirect to='/app'  />
    }
    const ClickLogin = () => {
      console.log();
    }

    const handleClose = () => {
      setOpen(false);
    };

    return(
      <Fragment>
        <Grid container component="main" className={classes.root}>
          <CssBaseline />
          <Grid item xs={false} sm={4} md={7} className={classes.image} />
          <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
            <div className={classes.paper}>
              <Avatar className={classes.avatar}>
                <LockOutlinedIcon />
              </Avatar>
              <Typography component="h1" variant="h5">
                KPI Monitor
              </Typography>
              <form className={classes.form} onSubmit={handleSubmit} noValidate>
                <TextField
                  value={username}
                  onChange={e => setEmail(e.target.value)}
                  variant="outlined"
                  margin="normal"
                  required
                  fullWidth
                  id="email"
                  label="Usuario"
                  name="email"
                  autoComplete="email"
                  autoFocus
                />
                <TextField
                  value={password}
                  onChange={e => setPassword(e.target.value)}
                  variant="outlined"
                  margin="normal"
                  required
                  fullWidth
                  name="password"
                  label="Contraseña"
                  type="password"
                  id="password"
                  autoComplete="current-password"
                />
                {/* <Link component={LINK} to="/app"> */}
                  <Button
                    disabled={!validateForm()}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                  >
                    Ingresar
                  </Button>
                {/* </Link> */}



                {/* <Grid container>
                  <Grid item xs>
                    <Link href="#" variant="body2">
                      Forgot password?
                    </Link>
                  </Grid>
                  <Grid item>
                    <Link href="#" variant="body2">
                      {"Don't have an account? Sign Up"}
                    </Link>
                  </Grid>
                </Grid> */}
                <Box mt={5}>
                    <Typography variant="body2" color="textSecondary" align="center">
                        {'Copyright © '}
                        {'Your Website'}
                        {' '}
                        {new Date().getFullYear()}
                        {'.'}
                    </Typography>
                </Box>
              </form>
            </div>
          </Grid>
        </Grid>
        <Dialog
                    open={open}
                    onClose={handleClose}
                    PaperComponent={PaperComponent}
                    aria-labelledby="draggable-dialog-title">
                    <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                        Error
                    </DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                       {mensaje}
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Okey
                    </Button>
                    </DialogActions>
                </Dialog>
      </Fragment>
    )
}

interface IProps{
  updateUsuario: (usuario: IUsuario) => void
  updateLogueado: (logueado: boolean) => void
}

export default LoginView