import React, { Component, Fragment } from 'react'
import { Link, Route } from 'react-router-dom'

import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import {AppBar, Typography, Toolbar} from '@material-ui/core'
import CargarData from './CargarData'
import { IFacturacion } from '../../../interfaces/IFacturacion';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
  }),
);

const ContentMain = (props: IProps) => {
    const classes = useStyles();
    

    return(
        <Fragment>
            <main className={classes.content}>
                <div className={classes.toolbar} />
                {/* <Link to="/1">Dash 1</Link>
                <Link to="/2">Dash 2</Link> */}
                
                <Route exact path='/app/cargar' > 
                    {/* <h1>vista para cargar el excel </h1>  */}
                    <CargarData 
                      dataValidated={props.dataValidated}
                      dataArchivo={props.dataArchivo}
                      updateArchivo={props.updateArchivo}/>
                </Route>
                <Route exact path='/app/dashboard' > 
                  <h1>vista dashboard </h1> 
                    
                </Route>
                <Route exact path='/app/usuarios' > 
                  <h1>vista usuarios </h1> 
                    
                </Route>

                <Route exact path='/app' > 
                  <CargarData 
                    dataValidated={props.dataValidated}
                    dataArchivo={props.dataArchivo}
                    updateArchivo={props.updateArchivo}/>
                    
                </Route>
            </main>
        </Fragment>
    )
}

interface IProps{
  dataValidated: boolean,
  dataArchivo: any,
  updateArchivo:  (facturaciones: IFacturacion[]) =>void
}

export default ContentMain