import React, { Component, Fragment, useEffect } from 'react'
import { Link, Route } from 'react-router-dom'
import moment from 'moment'
import Draggable from 'react-draggable';
import clsx from 'clsx'

import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import { Typography, Button, Grid, Dialog,DialogTitle, DialogContent, DialogContentText, DialogActions } from '@material-ui/core'
import Paper, { PaperProps } from '@material-ui/core/Paper';
import { Table,TableBody ,TableCell , TableRow ,TableContainer,  TableHead , TablePagination    } from  '@material-ui/core'
// import TableContainer from '@material-ui/core/TableContainer';
// import FileIcon from '@material-ui/icons/FileCopy'
import FileIcon from '@material-ui/icons/InsertDriveFile'
import CheckIcon from '@material-ui/icons/Check';
import SaveIcon from '@material-ui/icons/Save';
import { green } from '@material-ui/core/colors';
import { readFile, read, utils } from 'xlsx'


import {IFacturacion} from '../../../interfaces/IFacturacion'
import {insertarFacturacionesPorUsuario} from '../../../axios/FacturacionService'

function PaperComponent(props: PaperProps) {
    return (
      <Draggable handle="#draggable-dialog-title" cancel={'[class*="MuiDialogContent-root"]'}>
        <Paper {...props} />
      </Draggable>
    );
}
  
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    buttonSuccess: {
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[700],
        },
    },
  }),
);

const columns = [ 
    {id:1, nombre:'FINFO'}, 
    {id:2, nombre:'FECAPE'},
    {id:3, nombre:'FECCAN'},
    {id:4, nombre:'FECVEN'},
    {id:5, nombre:'CODCPT'},
    {id:6, nombre:'CODAPL'},
    // {id:7, nombre:'NUMCNT'},
    {id:8, nombre:'NUMSEC'},
    {id:9, nombre:'CODEPW'},
    {id:10, nombre:'CODEPP'},
    {id:11, nombre:'CODPRO'},
    {id:12, nombre:'CODOFI'},
    {id:13, nombre:'IMPOPE'},
    {id:14, nombre:'IMPSOL'},
    {id:15, nombre:'CODDIV'},
    {id:16, nombre:'TASOPE'},
    {id:17, nombre:'TASFON'},
    {id:18, nombre:'SPRNOR'},
    {id:19, nombre:'PLAOPE'},
    {id:20, nombre:'CODCLI'},
    {id:21, nombre:'TIPDOC'},
    {id:22, nombre:'NUMDOC'},
    {id:23, nombre:'CODSEG'},
    {id:24, nombre:'CANVEN'}

]

const CargarData = (props: IProps) => {
    const classes = useStyles();
    const [loading, setLoading] = React.useState(false);
    const [success, setSuccess] = React.useState(false);
    const [open, setOpen] = React.useState(false);
    const [open1, setOpen1] = React.useState(false);
    const [page, setPage] = React.useState(0);
    const [tabla, setTabla] = React.useState(false);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);
    
    const handleChangePage = (event: unknown, newPage: number) => {
        setPage(newPage);
    };
    const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };
    const timer:any = React.useRef<number>();


    const buttonClassname = clsx({
        [classes.buttonSuccess]: success,
    });

    useEffect(() => {
        return () => {
          clearTimeout(timer.current);
        };
      }, []);
    
    const printFileContents = (contents:any) =>{
        let lines = contents.split(/\n/);
        
        lines.forEach( (line:any) => console.log(line));
      }
    const onChange = (e: any) =>{

        // https://stackoverflow.com/questions/46909260/reading-excel-file-in-reactjs
        // var files = e.target.files, f = files[0];
        // var reader = new FileReader();
        // reader.onload = async function (e: any) {
        //     var data = e.target.result;
        //     let readedData = read(data, {type: 'binary'});
        //     const wsname = readedData.SheetNames[0];
        //     const ws = readedData.Sheets[wsname];
        //     console.log(ws)
        //     console.log(ws["!cols"])
        //     console.log(ws["!rows"])
            
        //     /* Convert array to json*/
        //     const dataParse = utils.sheet_to_json(ws, {header:1});
        //     console.log('m=> ', dataParse)
        //     dataParse.map( data => {
        //         console.log(data)
        //     })
        //     let token = localStorage.getItem("token");
        //     console.log('m=> ', token)
        //     // setFileUploaded(dataParse);
            
        // };
        // reader.readAsBinaryString(f)

        //https://es.stackoverflow.com/questions/119105/como-leo-un-archivo-txt-en-javascript-linea-por-linea
        var file = e.target.files[0];
        var reader = new FileReader();

        reader.readAsText(file);
        reader.onload = async function(e: any){
            var result: any =reader.result;
            var lineas = result.split('\n');

            let id = localStorage.getItem("id");
            var day = moment("15/01/2020", "MM-DD-YYYY");
            console.log(day)
            var dataAux:IFacturacion[] = []
            var ini = 0
            var error = false
            for(var linea of lineas){
                //console.log('[linea]', linea)
                
                var expresion = /\s*,\s*/
                var dataOfLine = linea.split(expresion);

                if(ini==0){
                    ini++
                    // console.log('Mil if')
                }else{
                    // console.log('Mil else')
                    let tasope = parseFloat(dataOfLine[15])
                    let tasfon = parseFloat(dataOfLine[16])
                    let sprnor = parseFloat(dataOfLine[17])
                    let plaope = parseFloat(dataOfLine[18])
                    let productoCodpro = dataOfLine[10]
                    let oficinaCodofi = dataOfLine[11]
                    let codcli = dataOfLine[19]
                    let suma = parseFloat((tasfon + sprnor).toFixed(5))

                    console.log({suma: (tasfon + sprnor).toFixed(5), tasope: tasope.toFixed(5) , tasfon, sprnor, plaope, productoCodpro, oficinaCodofi, codcli})
                    if(
                        // parseFloat(dataOfLine[15]) == (parseFloat(dataOfLine[16]) + parseFloat(dataOfLine[17])) &&
                        // parseFloat(dataOfLine[15]) > 0 && parseFloat(dataOfLine[15]) <= 1 &&
                        // parseFloat(dataOfLine[16]) > 0 && parseFloat(dataOfLine[16]) <= 1 &&
                        // parseFloat(dataOfLine[18]) > 0 &&
                        // dataOfLine[10] != "" && 
                        // dataOfLine[11] != "" && 
                        // dataOfLine[19] != "" 
                        tasope == suma &&
                        tasope > 0 && tasope <= 1 &&
                        tasfon > 0 && tasfon <= 1 &&
                        plaope > 0 &&
                        productoCodpro != "" &&
                        oficinaCodofi != "" &&
                        codcli != "" 
                    ){
                        console.log('if')
                        var aux:IFacturacion = {
                            usuarioId: parseInt(id!),
        
                            finfo: dataOfLine[0],
                            fecape: moment(dataOfLine[1], 'DD-MM-YYYY').format('MM-DD-YYYY') ,
                            feccan: moment(dataOfLine[2], 'DD-MM-YYYY').format('MM-DD-YYYY') ,
                            fecven: moment(dataOfLine[3], 'DD-MM-YYYY').format('MM-DD-YYYY') ,
                            fecpro: "20/10/2020",
                            codcpt: dataOfLine[4],
                            codapl: dataOfLine[5],
                            
                            // numcnt: dataOfLine[6],
        
                            numsec: dataOfLine[7],
                            codepw: dataOfLine[8],
                            codepp: dataOfLine[9],
        
                            productoCodpro: dataOfLine[10],
                            oficinaCodofi: dataOfLine[11],
        
                            impope: parseFloat(dataOfLine[12]) ,//f
                            impsol: parseFloat(dataOfLine[13]),//f
                            coddiv: dataOfLine[14],
                            tasope: parseFloat(dataOfLine[15]),//f
                            tasfon: parseFloat(dataOfLine[16]),//f
                            sprnor: parseFloat(dataOfLine[17]),//float
                            plaope: parseInt(dataOfLine[18]) ,//int
                            codcli: dataOfLine[19],
                            tipdoc: dataOfLine[20],
                            numdoc: dataOfLine[21],
        
                            segmentoCodseg: dataOfLine[22],
                            canalCarven: dataOfLine[23],
        
                            monto_pe: 0,
                            ing_pros: 0,
                            cos_pros: 0,
                            com_pros: 0,
                            rof_efan: 0,
                            tasa_imp_comp: 0,
                            saldo_apr_hold: 0,
                            riesgo_opera_pros: 0,
                            tasa_apr_hold: 0,
                            bdi_pros: 0,
                            cr_pros_hold: 0,
                            cr_local: 0,
                            comto_ce: 0,
                            monto_edad: 0
        
                        }
                        console.log('Mil', aux)
                        dataAux.push(aux)
                    }else{
                        console.log('else')
                        error=true
                    }

                    if(error){
                        setOpen(true);
                        return;
                    }
                    
                }
                
                //console.log(listaNombres)
            }
            if(error==false){
                setOpen1(true);
            }
            console.log('Milagros', dataAux)
            props.updateArchivo(dataAux)
        }
    }
    const handleButtonClick = async() => {
        console.log("click")
        console.log(props.dataArchivo)
        props.dataArchivo.map( async(facturacion) => {
            let result = await insertarFacturacionesPorUsuario(facturacion)
            console.log('result', result)
        })
        setTabla(false)
        props.updateArchivo([])
        //let result = await insertarFacturacionesPorUsuario()
    };

    const handleClose = () => {
        setOpen(false);
        if(open1==true){
            setTabla(true)
        }
        setOpen1(false);
      };

    return(
        <Fragment>
            <main className={classes.content}>
                <div>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <Typography>
                                Seleccionar Archivo 
                            </Typography>
                        </Grid>
                        <Grid item xs={6} sm={3}>
                            <input
                                accept=".txt, .csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                                className={classes.input}
                                id="contained-button-file"
                                onChange={(e) => onChange(e)}
                                type="file"/>
                            <label htmlFor="contained-button-file">
                                <Button variant="contained" color="primary" component="span" startIcon={<FileIcon />} className={classes.button}>
                                    Cargar Data
                                </Button>
                            </label>
                        </Grid>
                        <Grid item xs={6} sm={3}>
                            <Button variant="contained" color="primary" component="span" className={classes.button} onClick={handleButtonClick}>
                                Subir Data
                            </Button>
                        </Grid>

                        {/* "@material-ui/core": "^4.7.2",
                        "@types/material-ui": "^0.21.7", */}
                        <Grid item xs={12}>
                            {
                                (tabla==true) ? (
                                    <Paper>
                                        <TableContainer >
                                            <Table >
                                                <TableHead>
                                                    <TableRow>
                                                        {
                                                            columns.map(elemento=>(
                                                                <TableCell key={elemento.id}>
                                                                    {elemento.nombre}
                                                                </TableCell>
                                                            ))
                                                        }
                                                    
                                                    </TableRow>
                                                </TableHead>
                                                <TableBody>
                                                    {
                                                        props.dataArchivo.map(data=>(
                                                            <TableRow>
                                                                <TableCell key={data.finfo}>
                                                                    {data.finfo}
                                                                </TableCell>
                                                                <TableCell key={data.fecape}>
                                                                    {data.fecape}
                                                                </TableCell>
                                                                <TableCell key={data.feccan}>
                                                                    {data.feccan}
                                                                </TableCell>
                                                                <TableCell key={data.fecven}>
                                                                    {data.fecven}
                                                                </TableCell>
                                                                <TableCell key={data.codcpt}>
                                                                    {data.finfo}
                                                                </TableCell>
                                                                <TableCell key={data.codapl}>
                                                                    {data.finfo}
                                                                </TableCell>
                                                                {/* <TableCell key={data.}>
                                                                    {data.}
                                                                </TableCell> */}
                                                                <TableCell key={data.numsec}>
                                                                    {data.numsec}
                                                                </TableCell>
                                                                <TableCell key={data.codepw}>
                                                                    {data.codepw}
                                                                </TableCell>
                                                                <TableCell key={data.codepp}>
                                                                    {data.codepw}
                                                                </TableCell>
                                                                <TableCell key={data.productoCodpro}>
                                                                    {data.productoCodpro}
                                                                </TableCell>
                                                                <TableCell key={data.oficinaCodofi}>
                                                                    {data.oficinaCodofi}
                                                                </TableCell>
                                                                <TableCell key={data.impope}>
                                                                    {data.impope}
                                                                </TableCell>
                                                                <TableCell key={data.impsol}>
                                                                    {data.impsol}
                                                                </TableCell>
                                                                <TableCell key={data.coddiv}>
                                                                    {data.coddiv}
                                                                </TableCell>
                                                                <TableCell key={data.tasope}>
                                                                    {data.tasope}
                                                                </TableCell>
                                                                <TableCell key={data.tasfon}>
                                                                    {data.tasfon}
                                                                </TableCell>
                                                                <TableCell key={data.sprnor}>
                                                                    {data.sprnor}
                                                                </TableCell>
                                                                <TableCell key={data.plaope}>
                                                                    {data.plaope}
                                                                </TableCell>
                                                                <TableCell key={data.codcli}>
                                                                    {data.codcli}
                                                                </TableCell>
                                                                <TableCell key={data.tipdoc}>
                                                                    {data.tipdoc}
                                                                </TableCell>
                                                                <TableCell key={data.numdoc}>
                                                                    {data.numdoc}
                                                                </TableCell>
                                                                <TableCell key={data.segmentoCodseg}>
                                                                    {data.segmentoCodseg}
                                                                </TableCell>
                                                                <TableCell key={data.canalCarven}>
                                                                    {data.canalCarven}
                                                                </TableCell>

                                                            </TableRow>
                                                        ))
                                                    }
                                                    
                                                </TableBody>
                                            </Table>
                                        </TableContainer>
                                        {/* <TablePagination
                                            rowsPerPageOptions={[10, 25, 100]}
                                            component="div"
                                            count={props.dataArchivo.length}
                                            rowsPerPage={rowsPerPage}
                                            page={page}
                                            onChangePage={handleChangePage}
                                            onChangeRowsPerPage={handleChangeRowsPerPage}
                                            /> */}
                                    </Paper>
                                ): null
                            }
                        </Grid>
                    </Grid>
                    
                    

                    
                </div>
                <Dialog
                    open={open}
                    onClose={handleClose}
                    PaperComponent={PaperComponent}
                    aria-labelledby="draggable-dialog-title">
                    <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                        Error
                    </DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                        Se encontro un error en el formato del archivo adjunto. Por favor de revisar el archivo
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Okey
                    </Button>
                    </DialogActions>
                </Dialog>
                <Dialog
                    open={open1}
                    onClose={handleClose}
                    PaperComponent={PaperComponent}
                    aria-labelledby="draggable-dialog-title">
                    <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                        Success 
                    </DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                        La data seleccionada se cargo correctamente 
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Okey
                    </Button>
                    </DialogActions>
                </Dialog>
            </main>
        </Fragment>
    )
}

interface IProps{
    dataValidated: boolean,
    dataArchivo: IFacturacion[],
    updateArchivo: (facturaciones: IFacturacion[]) => void
}

export default CargarData