import React, { Component, Fragment, useEffect, useState } from 'react'
import {  Link as LINK, Route } from 'react-router-dom'
import moment from 'moment'
import Draggable from 'react-draggable';
import clsx from 'clsx'

import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import { Link, Typography, Button, Grid, Dialog,DialogTitle, DialogContent, DialogContentText, DialogActions } from '@material-ui/core'
import Paper, { PaperProps } from '@material-ui/core/Paper';
// import FileIcon from '@material-ui/icons/FileCopy'
import FileIcon from '@material-ui/icons/InsertDriveFile'
import CheckIcon from '@material-ui/icons/Check';
import SaveIcon from '@material-ui/icons/Save';
import { green } from '@material-ui/core/colors';
import { readFile, read, utils } from 'xlsx'
import {Card ,CardActionArea ,TextField,  CardMedia, CardContent, CardActions } from '@material-ui/core'

import {IFacturacion} from '../../../interfaces/IFacturacion'
import {listar, update, eliminarUser} from '../../../axios/UsuarioService'
import { IUsuario } from '../../../interfaces/IUsuario';

  
function PaperComponent(props: PaperProps) {
    return (
      <Draggable handle="#draggable-dialog-title" cancel={'[class*="MuiDialogContent-root"]'}>
        <Paper {...props} />
      </Draggable>
    );
}


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    buttonSuccess: {
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[700],
        },
    },
    root: {
        flexGrow: 1,
      },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
      },
      form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
      },
  }),
);



const ListUsers = (props: IProps) => {
    const classes = useStyles();
    const [users, setUsers] = useState([])
    const [registro, setRegistro] =useState(false)
    const [eliminar, setEliminar] = useState(false)
    const [open, setOpen] = React.useState(false);
    const [aviso, setAviso] = React.useState("");
    const [mensaje, setMensaje] = React.useState("");

    const [id, setId] = useState(0)
    const [username, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [nombre, setNombre] = useState("");
    const [apellido, setApellido] = useState("");
    const [correo, setCorreo] = useState("");
    const validateForm = () => (username.length > 0 && password.length > 0 && nombre.length > 0 && apellido.length > 0 && correo.length > 0);

    const NumerosEnCaracter = (texto:any): number=>{
        var numeros="0123456789";
        for(var i=0; i<texto.length; i++){
            if (numeros.indexOf(texto.charAt(i),0)!=-1){
               return 1;
            }
         }
         return 0;
    }

    useEffect( () => {
        async function ejecutar(){
            let result: any = await listar()
            setUsers(result.data)
            console.log('result', result)
        }
        ejecutar()
    }, [])
    
    const clickEditar = (id:number, user: any) => {
        console.log('click en editar', user)
        setId(id)
        setNombre(user.nombre)
        setApellido(user.apellido)
        setCorreo(user.correo)
        
        setRegistro(true);
    }

    const clickEliminar = (id: number) => {
        console.log('click en eleminar', id)
        setId(id)
        setEliminar(true)
       
    }
    const clickRegresar = () => {
        console.log('click en regresar')
        setRegistro(false)
    }

    const handleSubmit = async(event:any) => {
        event.preventDefault()
        
        var nom = NumerosEnCaracter(nombre)
        var ape = NumerosEnCaracter(apellido)
        if(nom == 1){
            setAviso('Error')
            setMensaje('Ingrese un nombre correcto porfavor')
            setOpen(true)
        }else if(ape == 1){
            setAviso('Error')
            setMensaje('Ingrese un apellido correcto porfavor')
            setOpen(true)
        }else{
            console.log("se war");
            var user :any ={
                id: id,
                username: username,
                password: apellido,
                nombre: nombre,
                apellido: apellido,
                correo: correo
            }
            const result: any = await update(user)
            console.log("operation", result.data);

            if(result.data == true){
                setAviso('Operación Correcta')
                setMensaje('Se actualizo correctamente al usuario')
                setOpen(true)
                setRegistro(false)

                let result: any = await listar()
                setUsers(result.data)
            }
            console.log("data", result.data);
            //props.updateLogueado(true)
            // return <Redirect to='/app'  />
        }
        
    }

    const handleClose = () => {
        setOpen(false);
        setEliminar(false)
    };

    const ConfirmarEliminacion = async() => {
        console.log('confirmo la elimnación de ', id)

        const result: any = await eliminarUser(id)
        console.log("operation", result.data);

        if(result.data == true){
            setEliminar(false)
            setAviso('Operación Correcta')
            setMensaje('Se elemino correctamente al usuario')
            
            setOpen(true)
            setRegistro(false)

            let result: any = await listar()
            setUsers(result.data)
        }else{
            setEliminar(false)
            setAviso('Error')
            setMensaje('Ocurrio un error al realizar la operación')

            setOpen(true)
        }

        setEliminar(false)
    }   
    return(
        <Fragment>
            {/* <main className={classes.content}> */}
                
                {
                    (registro== false) ?
                        <div className={classes.root}>
                            <Grid container spacing={3}>
                                {
                                    users.map((user:any)=>(
                                        <Grid item xs={3}>
                                            {/* <Paper className={classes.paper}>xs=3</Paper> */}
                                            {
                                                <Card className={classes.root} key={user.id}>
                                                    <CardActionArea>
                                                        <CardContent>
                                                            <Typography gutterBottom variant="h5" component="h2">
                                                                {user.nombre} 
                                                            </Typography>
                                                            <Typography variant="body2" color="textSecondary" component="p">
                                                                {user.apellido}
                                                            </Typography>
                                                            <Typography variant="body2" color="textSecondary" component="p">
                                                                {user.correo}
                                                            </Typography>
                                                        </CardContent>
                                                    </CardActionArea>
                                                    <CardActions>
                                                        {/* <Link component={LINK} to="/app/edit"> */}
                                                            <Button size="small" color="primary" onClick={()=>clickEditar(user.id, user)}>
                                                                Editar 
                                                            </Button>
                                                        
                                                        
                                                        <Button size="small" color="primary" onClick={()=>clickEliminar(user.id)}>
                                                            Eliminar
                                                        </Button>
                                                    </CardActions>
                                                </Card>
                                            }
                                        </Grid>
                                    ))
                                }
                                
                            </Grid>
                        </div>
                        :
                        <div className={classes.paper}>
                    
                            <Typography component="h1" variant="h5">
                                Ingrese los datos
                            </Typography>
                            <form className={classes.form} onSubmit={handleSubmit} noValidate>
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={6}>
                                <TextField
                                value={nombre}
                                onChange={e => setNombre(e.target.value)}
                                    autoComplete="fname"
                                    name="firstName"
                                    variant="outlined"
                                    required
                                    fullWidth
                                    type="text"
                                    id="firstName"
                                    label="Nombre"
                                    autoFocus
                                />
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                <TextField
                                value={apellido}
                                onChange={e => setApellido(e.target.value)}
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="lastName"
                                    label="Apellidos"
                                    name="lastName"
                                    autoComplete="lname"
                                />
                                </Grid>
                                <Grid item xs={12}>
                                <TextField
                                    value={correo}
                                    onChange={e => setCorreo(e.target.value)}
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="email"
                                    label="Correo"
                                    name="email"
                                    autoComplete="email"
                                />
                                </Grid>
                                <Grid item xs={12}>
                                <TextField
                                    value={username}
                                    onChange={e => setEmail(e.target.value)}
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="userName"
                                    label="User Name"
                                    name="userName"
                                    autoComplete="uname"
                                />
                                </Grid>
                                <Grid item xs={12}>
                                <TextField
                                    value={password}
                                    onChange={e => setPassword(e.target.value)}
                                    variant="outlined"
                                    required
                                    fullWidth
                                    name="password"
                                    label="Password"
                                    type="password"
                                    id="password"
                                    autoComplete="current-password"
                                />
                                </Grid>
                            </Grid>
                            <Button
                            disabled={!validateForm()}
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                className={classes.submit}
                            >
                                Guardar
                            </Button>
                            
                            <Button
                                onClick={e => clickRegresar()}
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                className={classes.submit}
                            >
                                Regeresar
                            </Button>
                            </form>
                        </div>

                }
                
                <Dialog
                    open={open}
                    onClose={handleClose}
                    PaperComponent={PaperComponent}
                    aria-labelledby="draggable-dialog-title">
                    <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                        {aviso}
                    </DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                       {mensaje}
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        
                    <Button onClick={handleClose} color="primary">
                        Okey
                    </Button>
                    </DialogActions>
                </Dialog>

                <Dialog
                    open={eliminar}
                    onClose={handleClose}
                    PaperComponent={PaperComponent}
                    aria-labelledby="draggable-dialog-title">
                    <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                        Cuidado 
                    </DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                       Esta Seguro que desea eliminar al usuario ?
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                    
                    <Button onClick={handleClose} color="primary">
                        Cancelar
                    </Button>
                    <Button onClick={ConfirmarEliminacion} color="primary">
                        Accepto
                    </Button>
                    </DialogActions>
                </Dialog>
            {/* </main> */}
        </Fragment>
    )
}

interface IProps{
}

export default ListUsers