import React, { Component, Fragment } from 'react'
import { Link, Route } from 'react-router-dom'
import { withStyles, Theme } from '@material-ui/core'

import ToolBarMain from '../components/ToolBarMain'
import MenuDrawerMain from '../components/MenuDrawerMain'
import ContentMain from '../components/ContentMain'

import {IUsuario} from '../../../interfaces/IUsuario'
import {IFacturacion} from '../../../interfaces/IFacturacion'

const styles = (theme: Theme) => ({
    root: {
        display: 'flex',
    }
});

class Main extends Component<IProps, IState> {
    constructor(props: IProps){
        super(props)
        this.state = {
            dataArchivo: [],
            dataValidated: false
        }
    }

    setFacturaciones = (facturaciones: IFacturacion[]) => {this.setState({dataArchivo: facturaciones})}

    render(){
        // const {classes: Theme} = this.props
        return(
            <Fragment>
                <div style={{ display: 'flex'}}>
                    <ToolBarMain updateLogueado={this.props.updateLogueado} usuario={this.props.usuario}/>
                    <MenuDrawerMain/>
                    <ContentMain   
                        dataValidated={this.state.dataValidated}
                        dataArchivo={this.state.dataArchivo}
                        updateArchivo={this.setFacturaciones}/>
                    {/* <Link to="/3">Dash 3</Link>
                    <Link to="/4">Dash 4</Link>

                    <Route path='/1'> 
                        <h1>EFE 1</h1> 
                    </Route>
                    <Route path='/2'> 
                        <h1>EFE 2</h1> 
                    </Route>

                    <Route path='/3'> 
                        <h1>EFE 3</h1> 
                    </Route>
                    <Route path='/4'> 
                        <h1>EFE 4</h1> 
                    </Route> */}
                </div>
               
                
            </Fragment>
        )
    }
}

interface IProps{
    usuario: IUsuario
    updateLogueado: (logueado: boolean) => void
}

interface IState{
    dataValidated: boolean,
    dataArchivo: IFacturacion[]
}

export default withStyles(styles)(Main); 