import React, { Component, Fragment, useEffect, useState } from 'react'
import { Link, Route } from 'react-router-dom'
import moment from 'moment'
import Draggable from 'react-draggable';
import clsx from 'clsx'

import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import { Container, CssBaseline, TextField, Typography, Button, Grid, Dialog,DialogTitle, DialogContent, DialogContentText, DialogActions } from '@material-ui/core'
import Paper, { PaperProps } from '@material-ui/core/Paper';
// import FileIcon from '@material-ui/icons/FileCopy'
import FileIcon from '@material-ui/icons/InsertDriveFile'
import CheckIcon from '@material-ui/icons/Check';
import SaveIcon from '@material-ui/icons/Save';
import { green } from '@material-ui/core/colors';
import { readFile, read, utils } from 'xlsx'
import {Card ,CardActionArea , CardMedia, CardContent, CardActions } from '@material-ui/core'

import {IFacturacion} from '../../../interfaces/IFacturacion'
import {registrar} from '../../../axios/UsuarioService'

import { IUsuario } from '../../../interfaces/IUsuario';

function PaperComponent(props: PaperProps) {
    return (
      <Draggable handle="#draggable-dialog-title" cancel={'[class*="MuiDialogContent-root"]'}>
        <Paper {...props} />
      </Draggable>
    );
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      },
      avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
      },
      form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
      },
      submit: {
        margin: theme.spacing(3, 0, 2),
      },
  }),
);



const UserEdit = (props: IProps) => {
    const classes = useStyles();
    const [users, setUsers] = useState([])
    const [open, setOpen] = React.useState(false);
    // const [registre, setRegistre] = React.useState(fasle);

    const [username, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [nombre, setNombre] = useState("");
    const [apellido, setApellido] = useState("");
    const [correo, setCorreo] = useState("");
    const validateForm = () => (username.length > 0 && password.length > 0 && nombre.length > 0 && apellido.length > 0 && correo.length > 0);
    const [aviso, setAviso] = React.useState("");
    const [mensaje, setMensaje] = React.useState("");

    const NumerosEnCaracter = (texto:any): number=>{
        var numeros="0123456789";
        for(var i=0; i<texto.length; i++){
            if (numeros.indexOf(texto.charAt(i),0)!=-1){
               return 1;
            }
         }
         return 0;
    }

    const handleSubmit = async(event:any) => {
        event.preventDefault()
        
        var nom = NumerosEnCaracter(nombre)
        var ape = NumerosEnCaracter(apellido)
        if(nom == 1){
            setAviso('Error')
            setMensaje('Ingrese un nombre correcto porfavor')
            setOpen(true)
        }else if(ape == 1){
            setAviso('Error')
            setMensaje('Ingrese un apellido correcto porfavor')
            setOpen(true)
        }else{
            console.log("se war");
            var user :any ={
                username: users,
                password: apellido,
                nombre: nombre,
                apellido: apellido,
                correo: correo
            }
            const result: any = await registrar(user)
            console.log("operation", result.data.operation);

            if(result.data.operation == true){
                setAviso('Operación Correcta')
                setMensaje('Se registró correctamente al usuario')
                setOpen(true)
            }
            console.log("data", result.data);
            //props.updateLogueado(true)
            // return <Redirect to='/app'  />
        }
        
      }

    const handleClose = () => {
        setOpen(false);
    };
    
    return(
        <Fragment>
            {/* <main className={classes.content}> */}
                
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                
                <div className={classes.paper}>
                    
                    <Typography component="h1" variant="h5">
                        Ingrese los datos
                    </Typography>
                    <form className={classes.form} onSubmit={handleSubmit} noValidate>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                        <TextField
                        value={nombre}
                        onChange={e => setNombre(e.target.value)}
                            autoComplete="fname"
                            name="firstName"
                            variant="outlined"
                            required
                            fullWidth
                            type="text"
                            id="firstName"
                            label="Nombre"
                            autoFocus
                        />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                        <TextField
                        value={apellido}
                        onChange={e => setApellido(e.target.value)}
                            variant="outlined"
                            required
                            fullWidth
                            id="lastName"
                            label="Apellidos"
                            name="lastName"
                            autoComplete="lname"
                        />
                        </Grid>
                        <Grid item xs={12}>
                        <TextField
                            value={correo}
                            onChange={e => setCorreo(e.target.value)}
                            variant="outlined"
                            required
                            fullWidth
                            id="email"
                            label="Correo"
                            name="email"
                            autoComplete="email"
                        />
                        </Grid>
                        <Grid item xs={12}>
                        <TextField
                            value={username}
                            onChange={e => setEmail(e.target.value)}
                            variant="outlined"
                            required
                            fullWidth
                            id="userName"
                            label="User Name"
                            name="userName"
                            autoComplete="uname"
                        />
                        </Grid>
                        <Grid item xs={12}>
                        <TextField
                            value={password}
                            onChange={e => setPassword(e.target.value)}
                            variant="outlined"
                            required
                            fullWidth
                            name="password"
                            label="Password"
                            type="password"
                            id="password"
                            autoComplete="current-password"
                        />
                        </Grid>
                    </Grid>
                    <Button
                        disabled={!validateForm()}
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}>
                        Guardar
                    </Button>
                    </form>
                </div>
                
            </Container>


            <Dialog
                    open={open}
                    onClose={handleClose}
                    PaperComponent={PaperComponent}
                    aria-labelledby="draggable-dialog-title">
                    <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                        {aviso}
                    </DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                       {mensaje}
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Okey
                    </Button>
                    </DialogActions>
                </Dialog>
            {/* </main> */}
        </Fragment>
    )
}

interface IProps{
}

export default UserEdit