import axiosInstance from './IAxios'
import {IFacturacion} from '../interfaces/IFacturacion'

const getFacturacionesPorUsuario = async (id: number): Promise<Array<IFacturacion>>=>{

    let data:Array<IFacturacion>  = await axiosInstance.get("facturacion/"+id)
   
    console.log('axios =>', data)
    return data
}

const insertarFacturacionesPorUsuario = async (facturacion: IFacturacion): Promise<boolean>=>{
    var resultado: boolean = false
    try{
        let data: boolean  = await axiosInstance.post("facturacion/", facturacion)
        console.log('axios =>', data)
        resultado = data
    }catch(e){
        console.log('axios error =>', e)
    }
    
   
    return resultado
}


export {
    getFacturacionesPorUsuario,
    insertarFacturacionesPorUsuario
}